import SlInput from '@shoelace-style/shoelace/dist/react/input';
import SlTextarea from '@shoelace-style/shoelace/dist/react/textarea';
import SlCheckbox from '@shoelace-style/shoelace/dist/react/checkbox';

import SlOption from '@shoelace-style/shoelace/dist/react/option';
import SlSelect from '@shoelace-style/shoelace/dist/react/select';

import React, { useEffect, useState } from 'react';
//import axios from 'axios';

const css = `
    sl-dialog {
        --width: 75rem;
    }
    .form-content {
        text-align: left;

        small, sl-input, sl-textarea, sl-checkbox {
            margin: 25px 0px;
            display: block;
        }
    }
    
 
`;


function CreateNewFix(props) {

  const [checkRecurring, setCheckRecurring] = useState(false)

  return (
    
    <div className="form-content">
    
      <small>For recurring maintenance, make sure to check and fillout the recurring checkbox</small>

      <SlInput 
        label="Title" 
        help-text="Main title for this task" 
        onSlChange={(e)=>props.setInputs({
            title: e.target.value,
            description: props.input.description,
            recurring: props.input.recurring,
            date: props.input.date,
            scheduled: props.input.scheduled,
            unit: props.input.unit,
            username: props.user.username
        })}
        clearable/>

  <SlSelect 
    placeholder="Select Unit/Apt/Address" 
    clearable label="Unit" 
    onSlChange={
      (e)=>{props.setInputs({
            title: props.input.title,
            description: props.input.description,
            recurring: props.input.recurring,
            date: props.input.date,
            scheduleddate: props.input.scheduleddate,
            scheduledtime: props.input.scheduledtime,
            unit: e.target.value,
            username: props.user.username
        })}}>

    {props.dataUnits && props.dataUnits.map(ele=><SlOption value={ele.title}>{ele.title}</SlOption>)}
  </SlSelect>


  <SlTextarea 
        label="Describe the issue" 
        resize="none" 
        help-text="Describe in few words the issue you are having" 
        onSlChange={(e)=>props.setInputs({
            title: props.input.title,
            description: e.target.value,
            recurring: props.input.recurring,
            date: props.input.date,
            scheduleddate: props.input.scheduleddate,
            scheduledtime: props.input.scheduledtime,
            unit: props.input.unit,
            username: props.user.username
        })}
      />
    <SlInput 
        label="Schedule Date" 
        help-text="Select Date" 
        type="date"
        min="2025-08-01"
        max="2025-12-31"
        onSlChange={(e)=>props.setInputs({
            title: props.input.title,
            description: props.input.description,
            recurring: props.input.recurring,
            date: props.input.date,
            scheduleddate: e.target.value,
            scheduledtime: props.input.scheduledtime,
            unit: props.input.unit,
            username: props.user.username
        })}
        clearable/>

<SlInput 
        label="Schedule Time" 
        help-text="Select Time" 
        type="time"
        min="07:00"
        max="17:00"
        onSlChange={(e)=>props.setInputs({
            title: props.input.title,
            description: props.input.description,
            recurring: props.input.recurring,
            date: props.input.date,
            scheduleddate: props.input.scheduleddate,
            scheduledtime: e.target.value,
            unit: props.input.unit,
            username: props.user.username
        })}
        clearable/>

    
      <SlCheckbox onSlChange={()=>setCheckRecurring(!checkRecurring)}> Is this a recurring task ?</SlCheckbox>
      {checkRecurring && <SlTextarea 
        label="Description" 
        resize="none" 
        help-text="Describe when this task should be performed. Once a week on Monday, etc." 
        onSlChange={(e)=>props.setInputs({
            title: props.input.title,
            description: props.input.description,
            recurring: e.target.value,
            date: props.input.date,
            scheduleddate: props.input.scheduleddate,
            scheduledtime: props.input.scheduledtime,
            unit: props.input.unit,
            username: props.user.username
        })}
      />}
      <style>{css}</style>
    </div>
  );
}

export default CreateNewFix;