import SlCard from '@shoelace-style/shoelace/dist/react/card';
import SlIcon from "@shoelace-style/shoelace/dist/react/icon";
import React, { useEffect, useState, useRef } from 'react';
//import axios from 'axios';
import { Grid } from "gridjs";


const css = `
.overviewunits {
  .table-wrapper {
    width: 1000px
  }
}

.no-data-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 265px;
  justify-content: center;
  gap: 21px;
}
`;
let dataTable;

function OverviewUnits(props) {
  //const [items, setItems] = useState([]);
  //const [newItemName, setNewItemName] = useState('');
  const wrapperRef = useRef(null);


  useEffect(() => {
    // initial setup
    //define data array
    if (!dataTable) {
        if (props.data && props.data.length > 0) {
            const grid = new Grid({
                columns: ["Title", "Address", "Description", "Date"],
                data: props.data
              }).render(wrapperRef.current);
        }
    }
    
  });

  return (
    <div className="overviewunits">
      <h3>Unit Overview</h3>
      <div ref={wrapperRef} id="example-table" className="table-wrapper"></div>
      {props.data.length === 0 && <div className="no-data-wrapper">
        <SlIcon name="database-exclamation"></SlIcon>
        <span>No Data Available. Click on <b>Create New Unit</b> to add one.</span>
        </div>}
      <style>{css}</style>
    </div>
  );
}



export default OverviewUnits;