//import SlButton from '@shoelace-style/shoelace/dist/react/button';
import SlCard from '@shoelace-style/shoelace/dist/react/card';
import SlIcon from "@shoelace-style/shoelace/dist/react/icon";
import React, { useEffect, useState, useRef } from 'react';
//import axios from 'axios';
import { Grid, html } from "gridjs";


const css = `
  .table-link-buttons {
    margin: 0px;
  }

  .overviewfix {
    .table-wrapper {
      width: 1000px
    }
  }

  .no-data-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 265px;
    justify-content: center;
    gap: 21px;
  }
  
`;
let dataTable;

function OverviewFix(props) {
  //const [items, setItems] = useState([]);
  //const [newItemName, setNewItemName] = useState('');
  const wrapperRef = useRef(null);




/*   { 
    name: 'Actions',
    formatter: (_, row) => html(`<a href='mailto:${row.cells[1].data}'>Email</a>`)
  } */

  useEffect(() => {

    if (!dataTable) {
        if (props.data && props.data.length > 0) {
            const grid = new Grid({
                columns: [{ 
      name: "AI Analysis",
      formatter: (cell) => html(`<sl-button class="table-link-buttons" variant="text" id="${cell}">See Report</sl-button>`)
    },"Title", "Description", "Recurring", "Date", "Unit", "scheduleddate", "scheduledtime"],
                data: props.data
              }).render(wrapperRef.current)
              grid.on('ready', (data) => console.log('Ready!!: ' + data));
        }
        setTimeout(() => {
          Array.from(document.querySelectorAll(".table-link-buttons")).forEach(ele=>{
            ele.addEventListener("click", props.openAI)
          })
        }, 1000);
        
    }
    
  });


  return (
    <div className="overviewfix">
      <h3>Task Overview</h3>
      <div ref={wrapperRef} id="example-table" className="table-wrapper"></div>
      {props.data.length === 0 && <div className="no-data-wrapper">
        <SlIcon name="database-exclamation"></SlIcon>
        <span>No Data Available. Click on <b>Create New Task</b> to add one.</span>
        </div>}
      <style>{css}</style>
    </div>
  );
}



export default OverviewFix;